<template>
	<div>
		{{ val }}
		<input type="text" :value=val>
		<button @click="$emit('getChangeVal','1')"></button>
	</div>
	
</template>

<script>
	export default{
		name:'inputBtn',
		//对应父组件中的绑定的val
		props: {
			val:Number,
		},
		
	}
	
</script>

<style>
</style>



